.booking-management {
  padding: 20px;
}

.booking-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.booking-table th,
.booking-table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: left;
}

.booking-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.booking-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.booking-table tr:hover {
  background-color: #f5f5f5;
}

.booking-table .delete-button {
  padding: 5px 10px;
  background-color: #f44336;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  align-self: end;
}

.delete-button:hover {
  background-color: #d32f2f;
}
.booking-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.booking-header h1 {
  margin: 0;
}

.sort-button {
  padding: 5px 10px;
  background-color: #4a90e2;
  color: white;
  border: none;
  cursor: pointer;
  width: 64px;
  font-size: 0.9em;
}
